.list {
  overflow-y: auto;
  height: 100%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 2.5% 0;
  .item {
    width: 40%;
    height: 0;
    margin: 2.5%;
    padding-bottom: calc(9/32*100%);
    position: relative;

    .view {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 1px solid #bfbfbf;
      border-radius: 4px;
    }
  }

  &.solo {
    padding: 0;
    .item {
      padding: 0;
      margin: 0;
      height: 100%;
      width: 100%;
    }
    .view {
      border: none;
      border-radius: 0;
    }
  }
}
